@import "./../../styles/variables.scss";


.details__container {
    display: flex;
    margin: 3rem 0 5rem;
    width: 100%;
}

.donation__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: auto;
    padding: 0px 5px;
    width: auto;
    font-weight: 500;
    font-size: 12px;
    margin-top: 30px;

    @media (max-width: 576px) {
        height: auto;
    }

    &__details__container {
        display: flex;
        justify-self: center;
        align-self: center;
    }

    &__donation__box {
        width: 100%;
        height: 425px;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
        position: relative;
        margin-left: auto;
        margin-right: auto;

        -webkit-transition: all 0.15s ease-out;
        -moz-transition: all 0.15s ease-out;
        -o-transition: all 0.15s ease-out;
        transition: all 0.15s ease-out;

        @media (max-width: 576px) {
            width: 315px;
        }


        &__title {
            background: rgba($dark, 0.6);
            width: 100%;
            border-top-left-radius: 4px;
            border-top-right-radius: 4px;
            color: white;
            text-align: center;
            padding-top: 12px;
            padding-bottom: 12px;
            font-size: 15px;
        }

        &__donateButton {
            background: rgba($dark, 0.9);
            width: 100%;
            color: white;
            text-align: center;
            padding: 12px 16px;
            box-shadow: 0px 4px 5.5px 0px rgba(0, 0, 0, 0.07);
            font-size: 18px;
            bottom: 0px;
            position: absolute;
            cursor: pointer;
            font-weight: 800;
        }

        &__donateButton:hover {
            filter: contrast(115%);
        }

        &__fields {
            width: 50%;
            display: block;
            position: absolute;
            top: 30px;
            left: 15px;

            input {
                width: 90%;
                font-size: 17px;
                padding: 10px;
                border-radius: 4px;
                border-width: 0px;
                color: #5C5C5C;
                font-weight: 500;
                margin-bottom: 10px;
                -webkit-font-smoothing: antialiased;
            }

            ::-webkit-input-placeholder {
                color: #cdcdcd;
                font-size: 15px;
            }

            :-moz-placeholder {
                /* Firefox 18- */
                color: #cdcdcd;
                font-size: 15px;
            }

            ::-moz-placeholder {
                /* Firefox 19+ */
                color: #cdcdcd;
                font-size: 15px;
            }

            :-ms-input-placeholder {
                color: #cdcdcd;
                font-size: 15px;
            }
        }

        &__amount {
            width: 40%;
            display: block;
            position: absolute;
            top: 30px;
            right: 15px;

            &__button {
                width: 100%;
                background-color: gray;
                margin-bottom: 0px;
                text-align: center;
                color: white;
                padding: 15px 0px 15px 0px;
                border-radius: 4px;
                font-size: 20px;
                cursor: pointer;

                -webkit-transition: 400ms background-color;
                -moz-transition: 800ms opacity, 800ms right;
                -ms-transition: 800ms opacity, 800ms right;
                -o-transition: 800ms opacity, 800ms right;
                transition: 200ms background-color;

                input {
                    min-width: 50px;
                    margin: 0 5px;
                    font-size: 15px;
                    font-weight: 500;
                    border: none;
                    background-color: transparent;
                    color: white;
                    font-stretch: normal;
                    font-style: normal;
                    font-variant: normal;
                    -webkit-font-smoothing: antialiased;

                    -webkit-transition: all 0.15s ease-out;
                    -moz-transition: all 0.15s ease-out;
                    -o-transition: all 0.15s ease-out;
                    transition: all 0.15s ease-out;
                }
            }

            &__button:not(:first-child) {
                margin: 5px 0;
            }

            &__button:hover {
                background-color: #393939;
            }

            &__selected {
                background-color: #393939;
            }

            &__setAmount {
                max-width: 55px;
            }

        }

    }
}